    <div class="block-white block-row flex">
      <img class="mr10" src="https://p9-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/e04c99427b38421dae4a6550eab7be68~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1" alt="">
      <div class="article-content">
        <div class="article-scri"></div>
        <div class="article-auth fr">--- </div>
      </div>
    </div>
    <div class="block-white block-row flex">
      <img style="width: 100%;height: auto;" src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1815629,828882053&fm=26&gp=0.jpg" alt="">
    </div>
    <!--  -->
    <div class="article-list-foo mt10">
      <div class="article-list" v-for="item in 5" :key="item">
        <div class="article-title"></div>
        <div class="article-c">
          <div class="article-content">
            <div class="article-scri clear">,</div>
            <div class="article-b">
              <span class="article-time">2020.01.05</span>
              <span class="article-prim ml10">1993</span>
              <span class="article-prim ml10"><i class="fz14"></i>710</span>
              <span class="article-auth">--- </span>
            </div>
          </div>
          <div class="imgFoo">
            <img src="https://p1-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/06e51fbf8d9d43beb0a0c8f6644b7af6~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="mt10 block-white" ref="chartFoo">
      <div class="block-title"></div>
      <canvas ref="myChart" id="myChart" width="100" height="260"></canvas>
    </div>
  </div>
</template>

<script>
import  { Swiper, SwiperSlide} from 'vue-awesome-swiper'
import 'swiper/swiper.scss'

const F2 = require('@antv/f2')

export default {
  name: "Home",
  components: {
    Swiper,
    SwiperSlide
  },
  props: {},
  data() {
    return {
      swiperOption: {
        loop: true,
        autoplay: {
          disableOnInteraction: false,
        },
        pagination: {
          el: '.swiper-pagination',
        },
        spaceBetween : 10,
      },
      swiperList: [
        { id: 1, scr: '', img: 'https://p1-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/0e6974e6001e479bbc39592901a7459d~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1' },
        { id: 2, scr: '', img: 'https://p3-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/cfcc6f8ea9984c7999a1a7e07f0ec74f~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1' },
        { id: 3, scr: '', img: 'https://p6-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/6e762c359c824a5cbea0c4aa3dae904d~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1' },
      ],
      weatherTodayData: {},
      weather_loading: true,
      aaa: ''
    }
  },
  created() {
    this.getAd()
    // this.apiTest()
  },
  mounted() {
    this.initChart()
  },
  methods: {
    getAd() {
      this.weather_loading = true

      let params = {
        key: '523630f6e97e4a4c08fcd91e9f005dd2',
      }
      this.$http.get('https://restapi.amap.com/v3/ip', {params}).then(res => {
        this.weatherTodayData.city = res.data.city
        this.getWeatherData(res.data.adcode)
        this.apiTest(res.data.rectangle)
      })
    },
    getWeatherData(city) {
      let params = {
        key: '523630f6e97e4a4c08fcd91e9f005dd2',
        city,
        extensions: 'all'
      }
      this.$http.get('https://restapi.amap.com/v3/weather/weatherInfo', {params}).then(res => {
        this.weatherTodayData = {
          ...res.data.forecasts[0].casts[0],
          province: res.data.forecasts[0].province,
          city: res.data.forecasts[0].city
        }
        this.weather_loading = false
      })
    },
    apiTest(rectangle) {
      console.log(rectangle)
      let params = {
        key: '523630f6e97e4a4c08fcd91e9f005dd2',
        zoom: 1,
        location: rectangle
      }
      this.$http.get('https://restapi.amap.com/v3/staticmap', {params}).then(res => {
        this.aaa = res.data
      })
    },
    initChart() {
      document.getElementById('myChart').style.width = this.$refs.chartFoo.offsetWidth + 'px'
      document.getElementById('myChart').style.height = '260px'
      const data = [
        { date: '2020-11-09', value: 10 },
        { date: '2020-11-10', value: 13 },
        { date: '2020-11-11', value: 15 },
        { date: '2020-11-12', value: 20 },
        { date: '2020-11-13', value: 21 },
        { date: '2020-11-14', value: 12 },
        { date: '2020-11-15', value: 26 },
        { date: '2020-11-16', value: 32 },
        
      ];

      const chart = new F2.Chart({
        id: 'myChart',
        pixelRatio: window.devicePixelRatio
      });

      chart.source(data, {
        value: {
          tickCount: 5,
          min: 0
        },
        date: {
          type: 'timeCat',
          range: [ 0, 1 ],
          tickCount: 4
        }
      });
      chart.tooltip({
        custom: true,
        showXTip: true,
        showYTip: true,
        snap: true,
        crosshairsType: 'xy',
        crosshairsStyle: {
          lineDash: [ 2 ]
        }
      });
      chart.axis('date', {
        label: function label(text, index, total) {
          const textCfg = {};
          if (index === 0) {
            textCfg.textAlign = 'left';
          } else if (index === total - 1) {
            textCfg.textAlign = 'right';
          }
          return textCfg;
        }
      });
      chart.line().position('date*value');
      chart.render();

    }
  },
};
</script>

